@import '../../variables.scss';

.ui-kit-types-and-color {
  display: flex;
  justify-content: space-between;
  max-width: 1114px;

  margin-top: 80px;

  margin-right: auto;
  margin-left: auto;

  &__container {
    max-width: 420px;
  }

  &__color-item {
    display: flex;
    max-width: 300px;
    align-items: center;

    margin-bottom: 20px;

    font-size: 19px;
    font-family: $headerFont;
    line-height: 29px;
  }

  &__label {
    font-weight: bold;
  }

  &__color-box {
    width: 70px;
    height: 70px;

    margin-right: 40px;

    border-radius: 6px;
  }

  &__dark-100 {
    background-color: $fontColor;
  }

  &__dark-75 {
    background-color: $fontColorLight75;
  }

  &__dark-50 {
    background-color: $fontColorLight50;
  }

  &__dark-25 {
    background-color: $fontColorLight25;
  }

  &__dark-5 {
    background-color: $fontColorLight5;
  }

  &__purple {
    background-color: $mainColor;
  }

  &__green {
    background-color: $secondColor;
  }

  &__type-item {
    display: flex;
    align-items: flex-start;

    margin-bottom: 32px;
  }

  &__tag-name {
    min-width: 40px;

    margin-right: 30px;

    text-align: right;
  }

  &__tag-h1 {
    color: $fontColorLight25;

    font-size: 24px;
    text-transform: uppercase;
  }

  &__tag-h2 {
    color: $fontColorLight25;

    font-size: 19px;
    text-transform: uppercase;
  }

  &__tag-h3 {
    color: $fontColorLight25;

    font-size: 12px;
    text-transform: uppercase;
  }

  &__tag-body {
    color: $fontColorLight25;

    font-size: 14px;
  }
}
