@import '../../variables.scss';

.list-of-checkboxes {
  display: block;
  width: 100%;

  &__header {
    display: block;
    width: 100%;
    position: relative;

    margin-bottom: 17px;
    padding-right: 25px;
  }

  &__title {
    color: $fontColor;

    font-size: 12px;
    font-weight: bold;
    line-height: 15px;
    text-transform: uppercase;
  }

  &_expandable &__header {
    cursor: pointer;
  }

  &_expandable &__header:focus {
    outline: auto;
    outline-width: 1px;
    outline-color: $mainColor;
  }

  &_expandable &__header::before {
    display: block;
    position: absolute;
    right: 14px;
    top: -6px;

    transform: rotateX(180deg);

    content: url('img/arrow-down.svg');
  }

  &__content {
    display: block;
  }

  &__item {
    margin-bottom: 10px;

    line-height: 18px;
  }
}

.list-of-checkboxes_expandable.list-of-checkboxes_close .list-of-checkboxes__header::before {
  transform: rotateX(0deg);
}

.list-of-checkboxes_expandable.list-of-checkboxes_close .list-of-checkboxes__content {
  display: none;
}
