@import '../../variables.scss';

.checkbox {
  color: $fontColorLight50;

  input {
    position: absolute;
    z-index: -1;
    opacity: 0;
  }

  label {
    display: flex;
    justify-content: start;

    cursor: pointer;
  }

  &__checkbox {
    display: block;
    width: 20px;
    height: 20px;
    flex-shrink: 0;

    border-radius: 4px;
    border: 1px solid $fontColorLight25;
    margin-right: 10px;
  }

  &__description {
    margin-top: 4px;

    color: $fontColorLight50;

    font-size: 12px;
    line-height: 15px;
  }

  input:focus + &__checkbox {
    border: 1px solid $mainColor;
  }

  input:checked + &__checkbox {
    border: 1px solid $mainColor;

    background-image: url('img/ok.svg');
    background-repeat: no-repeat;
    background-size: 20px 20px;
    background-position: 4px 2px;
  }

  &__label_bold {
    color: $fontColorLight75;

    font-weight: bold;
  }
}
