@import '../../variables.scss';

.slider__inner.noUi-target {
  display: block;
  width: 100%;
  height: 6px;

  border-radius: 3px;
  border: 1px solid $fontColorLight25;
  background-color: white;

  .noUi-connect {
    background: $secondColorGradient;
  }

  .noUi-connects {
    cursor: pointer;
  }

  .noUi-base {
    z-index: 0;
  }

  .noUi-handle {
    width: 12px;
    height: 12px;
    box-sizing: content-box;
    right: -7px;

    border: 2px solid white;
    border-radius: 10px;
    box-shadow: none;

    background: $secondColorGradient;
    outline: none;

    cursor: ew-resize;
  }

  .noUi-handle:focus {
    outline: auto;
    outline-width: 1px;
    outline-color: $mainColor;
  }

  .noUi-handle::before,
  .noUi-handle::after {
    display: none;
  }
}

.slider {
  width: 100%;

  &__info {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    margin-bottom: 6px;
  }

  &__title {
    display: block;

    text-transform: uppercase;
    font-size: 12px;
    line-height: 15px;
    font-weight: bold;
  }

  &__values {
    display: block;

    font-size: 12px;
    color: $fontColorLight50;
  }

  &__value-lower {
    margin-right: 3px;
  }

  &__description {
    margin-top: 10px;

    color: $fontColorLight50;

    font-size: 12px;
  }
}
