@import '../../variables.scss';

.ui-kit-form-elements {
  max-width: 1114px;

  margin-top: 80px;

  margin-right: auto;
  margin-left: auto;

  &__part-1,
  &__part-2,
  &__part-3 {
    display: flex;
    justify-content: space-between;

    margin-bottom: 40px;
  }

  &__col-1-3 {
    width: 30%;
    max-width: 300px;
  }

  &__col-2-3 {
    max-width: 700px;
  }

  &__element {
    margin-bottom: 20px;
  }

  &__like-buttons-container {
    display: flex;
  }

  &__like-button:not(:last-child) {
    margin-right: 10px;
  }

  &__rate-buttons-container {
    display: flex;
  }

  &__rate-button:not(:last-child) {
    margin-right: 10px;
  }

  &__buttons-container {
    display: flex;

    margin-bottom: 20px;
    margin-top: 20px;
  }

  &__button:not(:last-child) {
    margin-right: 10px;
  }

  &__opened-dropdown {
    margin-bottom: 140px;
  }

  &__opened-dropdown-with-actions {
    margin-bottom: 180px;
  }

  &__toggle {
    margin-bottom: 10px;
  }
}
