@import '../../variables.scss';

.pagination {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 240px;

  margin: 0 auto;

  line-height: 18px;

  &__actions {
    display: flex;
    width: 100%;
    flex-direction: row;
    align-items: center;
    flex-wrap: nowrap;
    justify-content: space-between;

    color: $fontColorLight50;

    font-size: 12px;
  }

  &__item-start,
  &__item-end {
    border-radius: 20px;

    background: $secondColorGradient;
  }

  &__item-start svg path,
  &__item-end svg path {
    fill: white;
  }

  &__item-start svg {
    transform: rotateY(180deg);
  }

  &__item {
    width: 40px;
    height: 40px;

    cursor: pointer;
  }

  &__filler {
    cursor: auto;
  }

  &__item_selected {
    border-radius: 20px;

    font-weight: bold;
    background: $mainColorGradient;
  }

  &__filler-ellipsis,
  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
  }

  &__link:active {
    color: $mainColor;
  }

  &__item_selected &__link {
    color: white;
  }

  &__link:focus {
    outline: auto;
    outline-width: 2px;
    outline-color: $mainColor;
  }

  &__description {
    display: block;
    width: 100%;

    margin-top: 10px;

    color: $fontColorLight75;

    font-size: 14px;
    text-align: center;
  }

  &_started &__item-start {
    display: none;
  }

  &_ended &__item-end {
    display: none;
  }
}
