@import '../../variables.scss';

.room-item {
  display: block;
  width: 100%;
  min-width: 270px;

  color: $fontColorLight75;

  box-shadow: 0 10px 20px $fontColorLight5;
  border-radius: 4px;

  line-height: 18px;

  &__info {
    padding: 20px 16px 20px 20px;
  }

  &__info-number-and-cost {
    display: flex;
    justify-content: space-between;
    align-items: center;

    border-bottom: 1px solid $fontColorLight5;
    padding-bottom: 10px;
    padding-top: 4px;
  }

  &__info-numero,
  &__info-room {
    font-family: $headerFont;
    font-size: 18px;
    line-height: 13px;
    font-weight: bold;
  }

  &__info-room {
    margin-right: 7px;
  }

  &__info-numero {
    margin-right: 6px;

    font-family: $mainFont;
    font-size: 14px;
  }

  &__info-number &__info-lux {
    color: $mainColor;

    font-size: 12px;
    text-transform: uppercase;
    font-weight: bold;
  }

  &__info-cost-container {
    color: $fontColorLight50;

    font-size: 14px;
    font-weight: bold;
  }

  &__info-cost {
    margin-right: 0;
  }

  &__info-cost-end {
    font-weight: normal;
    font-size: 12px;

    margin-left: 4px;
  }

  &__info-rate-and-feedback {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-top: 8px;
  }

  &__feedback-count {
    padding-top: 2px;

    color: $fontColorLight50;

    font-size: 14px;
    font-weight: bold;

    margin-right: 3px;
  }

  &__feedback-count-end {
    font-weight: normal;
    margin-left: 4px;
  }
}
