@import '../../variables.scss';

.rate-button {
  display: flex;
  flex-direction: row-reverse;

  &__item {
    display: block;
    width: 24px;
    height: 24px;

    padding: 2px;

    content: url('img/star-border.svg');
  }

  &__item_selected ~ li,
  &__item_selected {
    content: url('img/star-full.svg');
  }

  &_edit-enabled &__item {
    cursor: pointer;
  }

  &_edit-enabled &__item:hover,
  &_edit-enabled &__item:hover ~ li {
    content: url('img/star-full.svg');
  }
}
