@import '../../variables.scss';

.login-form {
  display: block;
  width: 380px;

  padding: 30px;
  padding-top: 42px;

  background-color: white;

  border: 1px solid rgba($color: #000, $alpha: 0.12);
  box-shadow: 0 10px 20px $fontColorLight5;
  border-radius: 4px;

  line-height: 14px;

  @media (max-width: 400px) {
    width: 100%;
  }

  &__input {
    margin-bottom: 10px;
  }

  &__title {
    display: block;

    margin-bottom: 23px;

    color: $fontColor;

    font-size: 23px;
  }

  &__button {
    margin-top: 20px;
  }

  &__footer {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-top: 30px;

    color: black;

    font-size: 14px;
    line-height: 24px;
  }

  &__register-link:focus {
    outline: auto;
    outline-width: 1px;
    outline-color: $mainColor;
  }
}
