@import '../../variables.scss';

.carousel {
  display: block;
  width: 100%;
  min-width: 270px;
  position: relative;
  overflow: hidden;

  &__items {
    display: flex;
    position: relative;
    width: inherit;

    transition: transform 0.4s ease-in-out;
  }

  &__item {
    width: 100%;
    min-width: 100%;
  }

  &__item-image {
    display: block;
    width: inherit;
    min-width: inherit;
  }

  &__side-actions {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }

  &__left-action,
  &__right-action {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    height: 100%;
    width: 42px;

    border: none;

    cursor: pointer;

    outline: none;
  }

  &__right-action {
    right: 0;

    transform: rotateY(180deg);

    background: linear-gradient(90deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0) 99.99%, rgba(255, 255, 255, 0) 100%);
  }

  &__left-action {
    background: linear-gradient(90deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0) 99.99%, rgba(255, 255, 255, 0) 100%);
  }

  &__indicators {
    display: flex;
    justify-content: end;
    align-items: center;
    position: absolute;
    right: 15px;
    bottom: 15px;
  }

  &__indicator {
    display: block;
    width: 8px;
    height: 8px;

    border: 1px solid white;
    border-radius: 4px;

    margin-left: 3px;
  }

  &__indicator_active {
    background-color: white;
  }
}

.carousel_top-rounded {
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}
